<template>
  <div class="index-wrap clearfix">
    <div class="index-left clearfix">
      <div class="index-left-block">
        <h2></h2>

        <template v-for="product in productList">
          <h3>{{product.title}}</h3>
          <ul>
            <li v-for="item in product.list"><a :href="item.url">{{item.name}}</a>
              <span v-show="item.hot" class="hot">HOT</span>
            </li>
          </ul>
          <div v-if="!product.last" class="hr"></div>
        </template>
      </div>
      <div class="index-left-block lastest-news">
        <h2></h2>
        <ul>
          <li v-for="item in newsList"><a :href="item.url" class="new-item">{{item.title}}</a></li>
        </ul>
      </div>
    </div>
    <div class="index-right">
      <slide-show class="clearfix" :slides="slides" :inv="inv"></slide-show>
      <div class="index-board-list">
        <div class="index-board-item clearfix" v-for="(item,index) in boardList" :class="[{'line-last':index%2 !== 0},'index-board-' + item.id]">
          <div class="index-board-item-inner">
            <h2>{{item.title}}</h2>
            <p>{{item.description}}</p>
            <div class="index-board-button">
              <a href="" class="button"></a>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import slideShow from '../components/slideShow'
export default {
  components: {
    slideShow
  },
  created() {
    this.$http.get('api/getNewsList')
    .then((res) => {
      this.newsList = res.data
    }, (err) => {
      console.log(err)
    })
  },
  data() {
    return {
      inv: 2000,
      slides: [
        {
          src: require('../assets/slideShow/pic1.jpg'),
          title: 'xxx1',
          href: '#/detail/analysis'
        },
        {
          src: require('../assets/slideShow/pic2.jpg'),
          title: 'xxx2',
          href: '#/detail/count'
        },
        {
          src: require('../assets/slideShow/pic3.jpg'),
          title: 'xxx3',
          href: '#/detail/publish'
        },
        {
          src: require('../assets/slideShow/pic4.jpg'),
          title: 'xxx4',
          href: '#/detail/forecast'
        }
      ],
      newsList: [],
      boardList: [
        {
          title: '',
          description: '',
          id: 'car',
          toKey: 'analysis',
          saleout: false
        },
        {
          title: '',
          description: '',
          id: 'earth',
          toKey: 'count',
          saleout: false
        },
        {
          title: '',
          description: '',
          id: 'loud',
          toKey: 'forecast',
          saleout: true
        },
        {
          title: '',
          description: '',
          id: 'hill',
          toKey: 'publish',
          saleout: false
        }
      ],
      productList: {
        pc: {
          title: 'PC',
          list: [
            {
              name: '',
              url: '#/detail/count'
            },
            {
              name: '',
              url: '#/detail/forecast'
            },
            {
              name: '',
              url: '#/detail/analysis',
              hot: true
            },
            {
              name: '',
              url: '#/detail/publish'
            }
          ]
        },
        app: {
          title: '',
          last: true,
          list: [
            {
              name: '91',
              url: 'http://weixin.com'
            },
            {
              name: '',
              url: 'http://twitter.com',
              hot: true
            },
            {
              name: '',
              url: 'http://maps.com'
            },
            {
              name: '',
              url: 'http://phone.com'
            }
          ]
        }
      }
    }
  }
}
</script>
  



